ul {
	list-style-type: circle;
	font-size: 20px;
	background-image: linear-gradient(to bottom right, gray, black);
  opacity: 0.95;
	font-family: Arial, sans-serif;
	padding-top: 20px;
}

li {
	padding-right: 10px;
	color: white;
}

.title {
		color: white;
		text-shadow: 1px 0 0 black, -2px 0 0 black, 0 2px 0 black, 0 -1px 0 black, 1px 1px white, -1px -1px 0 black, 1px -1px 0 black, -1px 1px 0 yellow;
		font-size: 22px;
}
