
.name {
    text-transform: uppercase;
    letter-spacing: 2px;
    word-spacing: 7px;
    text-align: center;
    padding: 20px 1px 10px 1px !important;
    text-shadow: -15px 5px 25px #ced0d3;
    font-family:  Courier New, monospace;
    box-shadow: 0px 1px 0 darkgrey, 6px 10px 20px black;
}

h4 {
    letter-spacing: 8px;
    word-spacing: 7px;
    text-align: center;
    font-style: italic;
    margin: 25px 0 !important;
    font-weight: bold;
}

